.TechnologyJarl {
  min-height: 100vh;
  padding-bottom: 20px;
  padding-top: 80px;
}

h2.TechnologyJarl-title {
  padding-top: 50px;
  padding-bottom: 50px;
  font-size: 50px;
  text-align: center;
}

.TechnologyJarl-container {
  width: 90%;
  margin: 0 auto;
  padding-top: 1rem;
}
.TechnologyJarl-tech {
  width: 90%;
  margin: 1rem auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  grid-gap: 15px;
}

.TechnologyJarl-item {
  height: 160px;
  padding: 1rem 1rem 0 1rem;
  border-radius: 4px;
  background-color: white;
}

.TechnologyJarl-item-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
}

.TechnologyJarl-media {
  height: 70%;
  width: 100%;
}

.TechnologyJarl-name {
  text-align: center;
  -ms-flex-item-align: end;
      align-self: flex-end;
  padding-bottom: 4px;
}



